  right: 15px;
  bottom: 15px;
  width: 32px;
  height: 32px;
  background-color: #fff;
  background-position: center center;
  background-repeat: no-repeat;
  border: 1px solid #454545;
  border-radius: 3px;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18"><path fill="#454545" d="M16.361,1.23846618 C14.919,-0.204533821 13.836,0.0114661786 13.836,0.0114661786 L7.784,6.06346618 L1.01,12.8364662 L0,17.5984662 L4.763,16.5884662 L11.537,9.81746618 L17.589,3.76546618 C17.588,3.76546618 17.805,2.68246618 16.361,1.23846618 Z M4.48,16.0164662 L2.856,16.3664662 C2.7,16.0734662 2.511,15.7804662 2.166,15.4344662 C1.82,15.0884662 1.527,14.9014662 1.234,14.7434662 L1.584,13.1204662 L2.054,12.6514662 C2.054,12.6514662 2.937,12.6694662 3.935,13.6674662 C4.932,14.6634662 4.951,15.5484662 4.951,15.5484662 L4.48,16.0164662 Z"/></svg>');
  opacity: .5;
  z-index: 1;
  transition: all .15s ease-in-out;
}

main #edit:focus {
  opacity: 1;
  background-color: #FEE298;
  border-color: transparent;
  outline: none;
}

main #edit:hover {
  opacity: 1;
  background-color: rgba(69, 69, 69, 0.5);
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18"><path fill="white" d="M16.361,1.23846618 C14.919,-0.204533821 13.836,0.0114661786 13.836,0.0114661786 L7.784,6.06346618 L1.01,12.8364662 L0,17.5984662 L4.763,16.5884662 L11.537,9.81746618 L17.589,3.76546618 C17.588,3.76546618 17.805,2.68246618 16.361,1.23846618 Z M4.48,16.0164662 L2.856,16.3664662 C2.7,16.0734662 2.511,15.7804662 2.166,15.4344662 C1.82,15.0884662 1.527,14.9014662 1.234,14.7434662 L1.584,13.1204662 L2.054,12.6514662 C2.054,12.6514662 2.937,12.6694662 3.935,13.6674662 C4.932,14.6634662 4.951,15.5484662 4.951,15.5484662 L4.48,16.0164662 Z"/></svg>');
