  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="20" height="12" viewBox="0 0 20 12"><path fill="white" d="M10,0.4 C3.439,0.4 0,5.232 0,6 C0,6.766 3.439,11.6 10,11.6 C16.56,11.6 20,6.766 20,6 C20,5.232 16.56,0.4 10,0.4 Z M10,10.307 C7.545,10.307 5.555,8.379 5.555,6 C5.555,3.621 7.545,1.691 10,1.691 C12.455,1.691 14.444,3.621 14.444,6 C14.444,8.379 12.455,10.307 10,10.307 Z M10,6 C9.593,5.553 10.663,3.846 10,3.846 C8.772,3.846 7.777,4.811 7.777,6 C7.777,7.189 8.772,8.154 10,8.154 C11.227,8.154 12.223,7.189 12.223,6 C12.223,5.453 10.346,6.379 10,6 Z"/></svg>');
