aside {
  width: 200px;
  float: left;
  height: calc(100vh - 23px);
  border-right: 1px solid #e6e6e6;
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
}

#divider {
  width: 5px;
  position: absolute;
  top: 0;
  right: -2px;
  bottom: 0;
  cursor: col-resize;
  z-index: 1000;
}

#sidebar button {
  width: 100%;
  height: 55px;
  display: block;
  border: 0;
  border-radius: 0;
  text-align: left;
  background-color: transparent;
  position: relative;
}

#sidebar button::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 25px;
  right: 0;
  height: 1px;
  background-color: #e6e6e6;
}

#sidebar button span {
  display: block;
  padding-left: 25px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

#sidebar button .side-title {
  font-size: 13px;
  font-weight: 700;
  padding-bottom: 2px;
  position: relative;
}

#sidebar button .side-text {
  font-size: 12px;
}

#sidebar button[data-favorite="true"] .side-title::before {
  content: '';
  display: block;
  width: 14px;
  height: 14px;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14"><polygon fill="rgba(69, 69, 69, 0.8)" points="12 22.668 7.674 25.315 8.854 20.382 5 17.086 10.056 16.684 12 12 13.944 16.684 19 17.086 15.146 20.382 16.326 25.315" transform="translate(-5 -12)"/></svg>') no-repeat center;
  position: absolute;
  top: 0;
  left: 4px;
}

#sidebar button:focus {
  outline: none;
  background-color: rgba(254, 226, 152, 0.5);
}

#sidebar button:focus::after {
  display: none;
}

#sidebar button.selected {
  outline: none;
  background-color: #FEE298;
}

#sidebar button.selected::after {
  display: none;
}

#sidebar button.above-selected::after {
  display: none;
}

.loader {
  height: calc(100vh - 23px);
  background: url('data:image/svg+xml;utf8,<svg version="1.1" id="loader-1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="40px" height="40px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50;" xml:space="preserve"><path fill="#fee298" d="M43.935,25.145c0-10.318-8.364-18.683-18.683-18.683c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615c8.072,0,14.615,6.543,14.615,14.615H43.935z"><animateTransform attributeType="xml" attributeName="transform" type="rotate" from="0 25 25" to="360 25 25" dur="0.6s" repeatCount="indefinite"/></path></svg>') no-repeat center center;
}

.CodeMirror {
  border: none;
  color: inherit;
  background: transparent;
  border-top: 1px solid #e6e6e6;
  position: absolute;
  top: 54px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 22px;
}

.CodeMirror .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
  background: none;
  background-image: none;
  background-position: 0 bottom;
  background-repeat: repeat-x;
  background-size: 5px 2px;
}

.CodeMirror.spellcheck .cm-spell-error:not(.cm-url):not(.cm-comment):not(.cm-tag):not(.cm-word) {
  background-image: linear-gradient(to right, red 50%, transparent 50%);
}

.CodeMirror-code {
  margin-top: 22px;
}

.CodeMirror-code.hide {
  opacity: 0;
}

.editor-toolbar {
  border: 0;
  border-radius: 0;
  height: 50px;
  margin-top: 3px;
  overflow: hidden;
  text-align: center;
  white-space: nowrap;
}

.editor-toolbar button {
  cursor: default;
  font-size: 15px;
}

.editor-preview {
  padding: 22px;
  background-image: url("data:image/png;base64,");
}

.CodeMirror-code * {
  font-size: inherit !important;
}

.editor-preview-active {
  user-select: text;
  font-weight: 400;
  width: 100%;
}

.editor-preview-active h1, .editor-preview-active h2, .editor-preview-active h3, .editor-preview-active h4, .editor-preview-active h5, .editor-preview-active h6 {
  line-height: 1;
  margin: 1ex 0;
}

.editor-preview-active h1:first-of-type {
  margin-top: 0;
}

.editor-preview-active p, .editor-preview-active blockquote, .editor-preview-active ul, .editor-preview-active ol, .editor-preview-active pre, .editor-preview-active hr, .editor-preview-active dl {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.editor-preview-active small {
  font-size: .8em;
}

.editor-preview-active sup, .editor-preview-active sub {
  font-size: .6em;
}

.editor-preview-active sup {
  vertical-align: super;
}

.editor-preview-active sub {
  vertical-align: sub;
}

.editor-preview-active table {
  margin-top: 1.5em;
  margin-bottom: 2em;
}

.editor-preview-active h1, .editor-preview-active h2, .editor-preview-active h3, .editor-preview-active h4, .editor-preview-active h5, .editor-preview-active h6, .editor-preview-active b, .editor-preview-active strong {
  font-weight: 700;
}

.editor-preview-active i, .editor-preview-active em {
  font-style: italic;
}

.editor-preview-active h1 {
  font-size: 2.2em;
}

.editor-preview-active h2 {
  font-size: 1.8em;
}

.editor-preview-active h3 {
  font-size: 1.6em;
}

.editor-preview-active h4 {
  font-size: 1.4em;
}

.editor-preview-active h5 {
  font-size: 1.2em;
}

.editor-preview-active h6 {
  font-size: 1.1em;
}

.editor-preview-active blockquote {
  padding-left: 2ch;
  border-left: 0.2em solid #FEE298;
}

.editor-preview-active ul {
  list-style-type: disc;
  margin-left: 2ch;
}

.editor-preview-active ul ul {
  list-style-type: circle;
}

.editor-preview-active ol {
  list-style-type: decimal;
  margin-left: 2ch;
}

.editor-preview-active li {
  margin-bottom: .6em;
}

.editor-preview-active dt {
  font-weight: 700;
  margin-top: .8em;
}

.editor-preview-active dd {
  margin-left: 2ch;
  display: list-item;
  list-style-type: disc;
}

.editor-preview-active code {
  background-color: rgba(230, 230, 230, 0.5);
  padding-left: 1ch;
  padding-right: 1ch;
  font-family: 'Menlo', monospace;
  border-radius: 0.3em;
}

.editor-preview-active pre {
  background-color: rgba(230, 230, 230, 0.5);
  padding: 1.5ch 2ch;
  border-radius: 0.4em;
  font-size: .9em;
  overflow-x: scroll;
}

.editor-preview-active pre code {
  background-color: transparent;
  padding: 0;
  white-space: pre;
}

.editor-preview-active hr {
  border: 1px solid #e6e6e6;
  border-top: 0;
}

.editor-preview-active a {
  color: #1E90FF;
  text-decoration: none;
  transition: color .15s;
}

.editor-preview-active a:hover {
  color: #0077ea;
}

.editor-preview-active a:focus {
  color: #fff;
  background-color: #1E90FF;
  outline: 3px solid #51a9ff;
}

.editor-preview-active img {
  display: inline-block;
  max-width: 100%;
}

.editor-preview-active table thead {
  background-color: #fff;
  font-weight: 700;
}

.editor-preview-active table tbody tr:nth-of-type(even) {
  background-color: rgba(69, 69, 69, 0.03);
}

.editor-preview-active table th, .editor-preview-active table td {
  padding: .8em;
}
